import React, { useEffect, useState } from 'react';
import { Link, useLocation } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

import './Navbar.css';

import MenuIcon from '@mui/icons-material/Menu';
import { Button } from '@mui/material';

function Navbar() {
    const { t, i18n } = useTranslation();

    const [webLanguage, setWebLanguage] = useState(i18n.language || 'eng');
    const [isNavOpen, setIsNavOpen] = useState(false);
    const location = useLocation()

    const handleLanguageChange = (newLanguage) => {
        i18n.changeLanguage(newLanguage);
        setWebLanguage(newLanguage);

        const newPathname = window.location.pathname.replace(`/${webLanguage}/`, `/${newLanguage}/`);
        window.history.replaceState(null, '', newPathname);
    };

    useEffect(() => {
        setIsNavOpen(false);
      }, [location]);
    

    return (
        <header>
            <div className='container'>
                <div className='navbar'>
                    <span className='logo'>L</span>
                    <div className={`navbar-item-holder ${isNavOpen ? 'open' : ''}`}>
                        <ul className={'navbar-items'}>
                            <li className='navbar-item'>
                                <Link to={`/${webLanguage}/`}>{t('Home')}</Link>
                            </li>
                            <li className='navbar-item'>
                                <Link to={`/${webLanguage}/about`}>{t('About Me')}</Link>
                            </li>
                        </ul>
                        <div className='language-selector'>
                            <Link to={`/${webLanguage}/lets-talk`}>{t('LetsTalk')}</Link>

                            <button onClick={() => handleLanguageChange('eng')}></button>
                            <button onClick={() => handleLanguageChange('geo')}></button>
                        </div>
                    </div>
                    <Button style={{ color: "white" }} className="navbar-toggler" onClick={() => {
                        setIsNavOpen((prev) => {
                            return !prev
                        })
                    }}>
                        <MenuIcon></MenuIcon>
                    </Button>
                </div>
            </div>
        </header>
    );
}

export default Navbar;
